input
  &:focus
    outline: none


.main
  padding: 0 18px
  width: 390px
  background: white
  margin: 100px auto
  min-height: 100%

@font-face
  font-family: roadradio
  src: url("./Fonts/roadradio_bold.otf")

@font-face
  font-family: roadradio700
  src: url("./Fonts/RoadRadio-700.ttf")

